@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Roboto:wght@300;400&display=swap');

html{
    font-size: 14px;
}

body {
    margin: 0;
    padding: 0;
    font-family: "Roboto";
    color: #fff;
    background-color: #2b2b2b;
    display: flex;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
}

*{
    box-sizing: border-box;
}

h2 {
    letter-spacing: 1.1px;
    font-weight: 400;
}

label {
    background: #444;
    text-align: center;
    display: inline-block;
    padding: 0.3em 1em;
    min-width: 8em;
    margin: 0.3em;
    font-size: 16px;
    cursor: pointer;
    border-radius: 1em;
  }

input[type="radio"]:checked +label {
    background-color: #365AEB;
  }

input[type="radio"] {
    appearance: none;
    margin: 0;
  }

.question-block {
    transition: 1s;
    background-color: #333;
    padding: 1em;
    border-radius: 2em;
    margin: 0.7em 2em;
}

#root {
    max-width: 65%;
    display: grid;
    place-content: center;
    min-height: 100vh;
}

button {
    margin: 1em 2em;
    padding: 1em;
    display: inline-block;
    border:none;
    background-color: #222;
    border-radius: 0.5em;
    color: white;
    font-size: 1.2em;
    cursor: pointer;
    transition: 0.3s;
}

button:hover {
    background-color: #333;
}

form {
    display: flex;
    flex-direction: column;
}

.App {
    display: grid;
    place-content: center;
}

.start {
    width: 100%;
    margin: 0;
}

@media (max-width: 600px) {
    #root {
        max-width: 100%;
        
    }
}